
html, body {
  font-family: 'Niramit', sans-serif;
}
.page {
  position: absolute;
  display: block;
}

.content-wrapper {
    position: relative;
    display: block;
    right: 0;
    top: 0;
    bottom: 0;
    min-height: 100%;
    z-index: 1;
    transition: .3s;
    margin-top: 40px;
    animation: slide-out-body 1s;
}
@media (max-width: 700px) {
.sidebar-active .content-wrapper {
    left: -220px;
    background: linear-gradient(125deg, white, #625abb73);
    filter: blur(10px);
}
}
.sidebar-active .content-wrapper {
  animation: slide-in 1s;
}
.sidebar-active {
  left: 220px;
}
.sidebar-active .sidebar-body ul li:nth-child(1) {
  animation: slide-in 1.1s;
}
.sidebar-active .sidebar-body ul li:nth-child(2) {
  animation: slide-in 1.3s;
}
.sidebar-active .sidebar-body ul li:nth-child(3) {
  animation: slide-in 1.5s;
}
.sidebar-active .sidebar-body ul li:nth-child(4) {
  animation: slide-in 1.6s;
}
ul {
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
  margin-top: 80px;
}
.sidebar ul li {
  list-style: none;
  padding-left: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
}
.sidebar ul li:last-child {
  border: none;
}
.sidebar-active .sidebar {
  animation: slide-in 1s;
  transform: translateX(0);
}
.page:not(.sidebar-active) .sidebar {
  animation: slide-out 1s;
}
.page:not(.sidebar-active) .sidebar-burger {
  animation: roll-out 1s;
}
.sidebar {
  -ms-flex-direction: column;
  -webkit-flex-direction: column;
  background: linear-gradient(rgb(243, 133, 120), rgba(31, 28, 236, 0.5));
  bottom: 0px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  left: 0;
  position: fixed;
  top: 0;
  transform: translateX(-220px);
  z-index: 100;
  width: 220px;
}
.sidebar-burger {
  position: absolute;
  left: 235px;
  top: 10px;
  animation: roll-in 1s;
  cursor: pointer;
  width: 40px;
  height: 40px;
}
.burger-inner {
  top: 50%;
  margin-top: -2px;
  transition-timing-function: cubic-bezier(.55,.055,.675,.19);
  transition-duration: .22s;
}
.sidebar-burger span,
.sidebar-burger span::before,
.sidebar-burger span::after {
  position: absolute;
  cursor: pointer;
  display: block;
  width: 40px;
  height: 4px;
  transition-timing-function: ease;
  transition-duration: .15s;
  transition-property: transform;
  border-radius: 4px;
  background-color: #d3b0d1;
}
.burger-inner:after,
.burger-inner:before {
    content: "";
}
.burger-inner:before {
  top: -10px;
}
.burger-inner:after {
  bottom: -10px;
}
.sidebar-burger.burger-active span {
  transition-delay: .12s;
  transition-timing-function: cubic-bezier(.215,.61,.355,1);
  transform: rotate(225deg);
}
.sidebar-burger.burger-active span:before {
  top: 0;
  transition: top .1s ease-out,opacity .1s ease-out .12s;
  opacity: 0;
}
.sidebar-burger.burger-active span:after {
  bottom: 0;
  transition: bottom .1s ease-out,transform .22s cubic-bezier(.215,.61,.355,1) .12s;
  transform: rotate(-90deg);
}
.sidebar li a {
  font-size: 30px;
  font-family: 'Niramit', sans-serif;
  cursor: pointer;
  color: #232121;
  transition: .3s ease-in;
}
.sidebar li a::after {
  content: "";
  width: 0;
  opacity: 0;
  position: absolute;
  left: 5px;
  margin-top: 28px;
  height: 2px;
  background: rgba(255, 255, 255, 0.61);
  transition: .3s ease-in;
}
.sidebar li a:hover,
.sidebar li a:active,
.sidebar li a:focus,
.sidebar li a:visited {
  margin-left: 10px;
}
.sidebar li a:hover:after,
.sidebar li a:visited:after,
.sidebar li a:active:after,
.sidebar li a:focus:after {
  opacity: 1;
  width: 10px;
}
.sidebar-legend {
  text-align: center;
  padding: 25px;
  cursor: pointer;
}
.sidebar-legend:hover a:before{
  width: 110px;
  left: 50px;
}
.sidebar-legend a {
  font-size: 40px;
  cursor: pointer;
  letter-spacing: 10px;
  font-family: Nova Round,sans-serif;
  color: #ffffff;
  text-shadow: 4px 1px 0 #ffa79c;
}
.sidebar-legend a:before {
  content: "";
  position: absolute;
  width: 80px;
  margin-top: 50px;
  left: 65px;
  height: 1px;
  background: #fdfdfd;
  transition: .3s ease-in;
}
.sidebar-legend a:after {
  content: "web";
  top: 85px;
  position: absolute;
  color: #fff;
  font-size: 10px;
  font-family: Round Nova,sans-serif;
  text-shadow: none;
  left: 85px;
}
.main-player-block {
    position: fixed;
    -webkit-align-items: center;
    align-items: center;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    bottom: 0;
    height: 80px;
    width: 100%;
    left: 0;
    right: 0;
    background: #fff;
    z-index: 999999;
    box-shadow: 0 -3px 15px 0px rgba(51, 51, 51, 0.1);
}
.player-trackline {
    padding: 0 40px;
    width: 80%;
    max-width: 800px;
    margin: 0 auto;
}
.timeline {
  position: relative;
  display: flex;
  align-items: center;
}
.player-settings {
  padding-right: 20px;
}
.player-buttons {
    width: 160px;
    padding: 0 20px;
    -webkit-align-items: center;
    align-items: center;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}
.player-buttons i {
  font-size: 20px;
  margin: 0 20px;
  cursor: pointer;
  color: rgb(58, 54, 84)
}
.player-buttons i:nth-child(2) {
  font-size: 30px;
}
.player-buttons i:first-child {
  margin-right: 20px;
  margin-left: 15px;
}
.current-time {
    left: 0;
}
.current-time, .total-time {
    position: relative;
    top: -10px;
    font-size: 10px;
    cursor: initial;
}
.total-time {
    margin-left: calc(100% - 41px);
}
.player-trackline .pin {
    background-color: white;
    border-radius: 8px;
    height: 8px;
    position: absolute;
    pointer-events: all;
    top: -3px;
    width: 8px;
    -webkit-box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.32);
    -moz-box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.32);
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.32);
    -webkit-transition: transform 0.25s ease;
    -moz-transition: transform 0.25s ease;
    -ms-transition: transform 0.25s ease;
    -o-transition: transform 0.25s ease;
    transition: transform 0.25s ease;
}
.player-trackline .slider {
  width: 100%;
  cursor: pointer;
  background: rgba(58, 54, 84, 0.5215686274509804);
  height: 2px;
  position: absolute;
}
.player-trackline .slider:hover {
  height: 4px;
  border-radius: 5px;
}
.player-trackline .slider:hover .pin {
  height: 10px;
}
.player-trackline .pin:active,
.player-trackline .pin:focus {
    -moz-transform: scale(1.5);
    -o-transform: scale(1.5);
    -ms-transform: scale(1.5);
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
}
:focus {
  outline: none;
}
@keyframes slide-in {
from {transform: translateX(-300px);
}
to {transform: translateX(0px);
}
}
@keyframes slide-out-body {
10% {transform: translateX(50px);
}
100% {transform: translateX(0px);
}
}
@keyframes slide-out {
from {transform: translateX(0px);
}
to {transform: translateX(-220px);
}
}
@keyframes roll-in {
from {transform: translateX(-0px) rotate(-360deg);
}
to {transform: translateX(0px) rotate(0);
}
}
@keyframes roll-out {
from {transform: translateX(0px) rotate(360deg);
}
to {transform: translateX(-0px) rotate(0);
}
}
@keyframes slide-up {
from {transform: translateY(900px);
}
to {transform: translateY(0px);
}
}

.social-block {
    text-align: center;
}
.separator {
    position: relative;
    text-align: center;
    width: 50px;
    color: #b775b3;
    margin: 0px auto;
    margin-top: -10px;
}
.loginActive .separator {
    position: relative;
    text-align: center;
    width: 50px;
    margin: 15px auto;
    color: #b775b3;
}
.separator:before {
    content: "";
    width: 12px;
    height: 1px;
    background: #b775b3;
    position: absolute;
    top: 15px;
    right: 0;
}
.separator:after {
    content: "";
    width: 12px;
    height: 1px;
    background: #b775b3;
    position: absolute;
    top: 15px;
    left: 0;
}
.login-form, .signup-form {
  transition: 0.3s;
  position: absolute;
}
.loginActive .login-form {
    width: 100%;
    opacity: 1;
    transform: translateX(0);
}
.login-form-content:not(.loginActive) {
    min-height: 550px;
}
.login-form-content:not(.loginActive) .signup-form {
    width: 100%;
    opacity: 1;
    transform: translateX(0);
}
.loginActive .signup-form {
  transform: translateX(500px);
  opacity: 0;
  width: 0;
}
.login-form-content:not(.loginActive) .login-form {
  transform: translateX(-500px);
  opacity: 0;
  width: 0;
}
.login-form .input-group {
    margin: 25px auto;
}
.input-group {
    width: 60%;
    margin: 10px auto;
}
.input-group .label {
    font-size: 16px;
    text-transform: lowercase;
    position: relative;
    pointer-events: none;
}
.input-group .input-field {
    outline: none;
    display: block;
    width: 100%;
    border: 0;
    box-shadow: 0 1px 0 0 rgba(157, 113, 220, 0.6);
    box-sizing: border-box;
    color: rgba(0, 0, 0, 0.6);
    font-family: inherit;
    font-size: inherit;
    font-weight: 500;
    line-height: inherit;
    margin-top: -5px;
    transition: 0.5s box-shadow, border-bottom;
}
.input-group .input-field:active,
.input-group .input-field:focus {
    border-bottom: 1px transparent;
    box-shadow: 0 2px 0 0 rgba(160, 113, 216, 0.62)
}
.input-group .button-gradient:hover {
    background: linear-gradient(335deg, #9670e7 0%, #f57f52 100%);
    box-shadow: 0px 11px 8em rgba(167, 151, 255, 0.63);
}
.input-group .button-gradient {
    color: white;
    background: linear-gradient(195deg, #9670e7 0%, #e87d67 100%);
}
.centered {
    text-align: center;
}
button {
    display: inline-block;
    zoom: 1;
    outline: 0;
    line-height: normal;
    white-space: nowrap;
    vertical-align: baseline;
    text-align: center;
    cursor: pointer;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.button-form {
    border-radius: 20px;
    border: 0;
    position: relative;
    background: none;
    border-radius: 20px;
    padding: 1.2em 5em;
    letter-spacing: 0.35em;
    font-size: 0.7em;
    transition: background-color 0.3s, box-shadow 0.3s, color 0.3s;
    margin: 1em;
}
.button-google {
    border: 1px solid rgba(219, 68, 55, 0.42);
    color: #db4437;
    padding-right: 3em;
}
.button-google:hover {
    box-shadow: 1px 1px rgba(195, 94, 35, 0.4);
}
.button-facebook:hover {
    box-shadow: 1px 1px rgba(80, 73, 169, 0.4);
}
.button-facebook {
    color: #443ca7;
    padding-right: 2em;
    border: 1px solid rgba(80, 73, 169, 0.4);
}
.form-button-brand {
    position: absolute;
    left: 20px;
}
.login-form-block {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
}
.login-form-header {
  border-bottom: 1px solid #f1f1f1;
  display: flex;
}
.left-header, .right-header {
    padding: 20px 0;
    text-align: center;
    width: 50%;
    cursor: pointer;
    left: 0;
    background: -webkit-linear-gradient(30deg, rgb(243, 133, 120), rgba(31, 28, 236, 0.88));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: initial;
    font-weight: 300;
    font-size: 23px;
    text-transform: uppercase;
}
.right-header {
    border-left: 1px solid #e6e6e6;
}
.login-form-content {
  position: relative;
  margin: auto auto;
  width: 50%;
  min-height: 500px;
  max-width: 500px;
  min-width: 300px;
  transition: 0.3s min-height;
  overflow: hidden;
  animation: fade-in 1s;
  border-radius: 2px;
  border: 1px solid rgba(111, 111, 111, 0.01);
  -moz-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.18), 0 15px 12px rgba(0, 0, 0, 0);
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.18), 0 15px 12px rgba(0, 0, 0, 0);
}
.login-form-body {
    position: relative;
    top: 10px;
}
